.DashbDIV {
    display:flex;
    flex-direction: column;
}

.AppointDiv {
    width: 1110px;
    height: 410px;
    margin: 30px 165px 30px 165px;
    background-color: white;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
}

.NameSize {
    margin: 24px 24px 18px 24px;
    font-weight: bold;
    font-size: 18px;
}

.articles {
    display: flex;
    flex-direction: row;
    width: 1110px;
    height: 56px;
    background-color: #E8E8E8;
}

.articles div:nth-child(n) {

    text-align: left;
}

.articles div:first-child {
    margin: auto 24px auto 68px;
    width: 180px;
}

.articles div:nth-child(2) {
    margin: auto 24px auto 0;
    width: 194px;
}

.articles div:nth-child(3) {
    margin: auto 24px auto 0;
    width: 96px;
}

.articles div:nth-child(4) {
    margin: auto 24px auto 0;
    width: 104px;
}

.articles div:nth-child(5) {
    margin: auto 24px auto 0;
    width: 122px;
}

.articles div:nth-child(6) {
    margin: auto 24px auto 0;
    width: 128px;
    margin-right: 98px;
}

.users {
    display: flex;
    flex-direction: column;
    
}

.user {
    display: flex;
    flex-direction: row;
    border-top: 1px solid rgb(156, 153, 153);
    height: 56px;
    
}
.user div {
    text-align: left;
}

.avatar {
    width: 32px;
    height: 32px;
}

.marginAvatar {
    margin: auto 12px auto 24px;
}

.fullName {
    width: 180px;
    margin: auto 24px auto 0;
}

.email {
    width: 194px;
    margin: auto 24px auto 0;
}

.date {
    width: 96px;
    margin: auto 24px auto 0;
}

.visitTime {
    width: 104px;
    margin: auto 24px auto 0;
}

.doctor {
    width: 122px;
    margin: auto 24px auto 0;
}

.conditions {
    width: 128px;
    margin: auto 26px auto 0;
}

.editIco {
    margin: auto 14px auto 0;
}

.delIco {
    margin: auto 26px auto 0;
}